.list {
    list-style: none;
    text-decoration: none;
    padding: 0;
    margin: 0;
}

h1,
h2,
h3 {
    margin: 0;
    color: inherit;
}

p {
    margin-top: 0;
    margin-bottom: 0;
}

img {
    max-width: 100%;
    width: 100%;
    height: 100%;
    display: block;
}

a {
    color: inherit;
}