.nav {
padding-top: 30px;
padding-bottom: 30px;
display: flex;
}
.nav-list {
  display: flex;
  margin-left: 285px;
}
.nav-list__item {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;

  text-transform: uppercase;

  color: #000;
  &:not(:last-child) {
    margin-right: 30px;
  }
}
.nav-icons {
  display: flex;
  margin-left: 285px;
}
.nav-icons__item{
    &:not(:last-child) {
            margin-right: 15px;
        }
}
